.progress-bar {
  display: inline-block;
  background: var(--progress-bar-background);
  border: 1px solid var(--progress-bar-border);
  border-radius: 3px;
  width: 100%;
  height: 1em;
  overflow: hidden;
}

.progress-bar-complete {
  height: 100%;
  background: var(--progress-bar-color);
}
